import {Action} from '@ngrx/store';
import {VehicleModel} from '../models/vehicle.model';
import {TransportationModel} from '../models/transportation.model';

// transportation actions
export const TRANSPORTATION_EDIT = 'TRANSPORTATION_EDIT';
export const SAVE_VEHICLES = 'SAVE_VEHICLES';
export const SET_VEHICLES = 'SET_VEHICLES';
export const SET_TRANSPORTATION = 'SET_TRANSPORTATION';
export const TRY_FETCH_VEHICLES = 'TRY_FETCH_VEHICLES';
export const TRY_FETCH_TRANSPORTATION = 'TRY_FETCH_TRANSPORTATION';
export const UPLOAD_TRANSPORTATION_IMAGES = 'UPLOAD_TRANSPORTATION_IMAGES';
export const UPLOAD_VEHICLE_IMAGES = 'UPLOAD_VEHICLE_IMAGES';
export const SET_CURRENT_VEHICLE = 'SET_CURRENT_VEHICLE';

// transportation classses

export class UploadVehicleImages implements Action {
  readonly type = UPLOAD_VEHICLE_IMAGES;

  constructor(public payload: any) {
  }
}

export class UploadTransportationImages implements Action {
  readonly type = UPLOAD_TRANSPORTATION_IMAGES;

  constructor(public payload: any) {
  }
}


export class TryFetchTransportation implements Action {
  readonly type = TRY_FETCH_TRANSPORTATION;
}

export class SetTransportation implements Action {
  readonly type = SET_TRANSPORTATION;

  constructor(public payload: TransportationModel) {
  }
}

export class TransportationEdit implements Action {
  readonly type = TRANSPORTATION_EDIT;

  constructor(public payload: TransportationModel) {
  }
}

export class SaveVehicles implements Action {
  readonly type = SAVE_VEHICLES;

  constructor(public payload: {}) {
  }
}

export class SetVehicles implements Action {
  readonly type = SET_VEHICLES;

  constructor(public payload: VehicleModel[]) {
  }
}

export class TryFetchVehicles implements Action {
  readonly type = TRY_FETCH_VEHICLES;
}

export class SetCurrentVehicle implements Action {
  readonly type = SET_CURRENT_VEHICLE;

  constructor(public payload: VehicleModel) {
  }

}

export type TransportationActions = UploadVehicleImages |
  SetCurrentVehicle |
  UploadTransportationImages |
  TryFetchTransportation |
  SetTransportation |
  TransportationEdit |
  SetVehicles |
  TryFetchVehicles |
  SaveVehicles;
