import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {map, switchMap} from 'rxjs/operators';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../../shared/serverEndpoints';
import * as TransportationActions from './transportation.actions';
import {ActivatedRoute, Router} from '@angular/router';

@Injectable()
export class TransportationEffects {
  constructor(
    public action: Actions,
    public http: HttpClient,
    private router: Router,
    private route: ActivatedRoute
  ) {
  }

  @Effect()
  uploadVehicleImages = this.action.pipe(
    ofType(TransportationActions.UPLOAD_VEHICLE_IMAGES),
    map((data: TransportationActions.UploadVehicleImages) => {
      return data.payload;
    }),
    switchMap((data: { vehicleRcImage: File, vehicleInsuranceImage: File, vehiclePucImage: File, vehicleFitnessImage: File }) => {
      return this.http.post(endPoints.uploadVehicleImages, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: TransportationActions.SET_CURRENT_VEHICLE,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  uploadTransportationImages = this.action.pipe(
    ofType(TransportationActions.UPLOAD_TRANSPORTATION_IMAGES),
    map((data: TransportationActions.UploadTransportationImages) => {
      return data.payload;
    }),
    switchMap((data: { registrationCinImage: File, registrationGstImage: File, registrationMsmeImage: File, registrationRcImage: File }) => {
      return this.http.post(endPoints.uploadTransportationImages, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: TransportationActions.SET_TRANSPORTATION,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );


  @Effect()
  fetchTransportation = this.action.pipe(
    ofType(TransportationActions.TRY_FETCH_TRANSPORTATION),
    switchMap(() => {
      return this.http.get(endPoints.fetchTransportation, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        console.log('fetching transporatation');
        return [
          {
            type: TransportationActions.SET_TRANSPORTATION,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  fetchVehicles = this.action.pipe(
    ofType(TransportationActions.TRY_FETCH_VEHICLES),
    switchMap(() => {
      return this.http.get(endPoints.fetchVehicles, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {type: TransportationActions.SET_VEHICLES, payload: response.body}
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  SaveVehicles = this.action.pipe(
    ofType(TransportationActions.SAVE_VEHICLES),
    map((payload: TransportationActions.SaveVehicles) => {
      return payload.payload;
    }),
    switchMap((data) => {
      return this.http.post(endPoints.saveVehicles, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        this.router.navigate(['/roles/b/transportationBusiness/saveVehicle']);
        return [
          {type: TransportationActions.SET_CURRENT_VEHICLE, payload: response.body}
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  editTransportation = this.action.pipe(
    ofType(TransportationActions.TRANSPORTATION_EDIT),
    map((payload: TransportationActions.TransportationEdit) => {
      console.log(1);
      return payload.payload;
    }),
    switchMap((data) => {
      console.log(2);
      return this.http.post(endPoints.editTransportation, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: TransportationActions.SET_TRANSPORTATION,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

}
