import {NgModule} from '@angular/core'; 
import {Routes, RouterModule} from '@angular/router';
import {TransportationComponent} from './transportation.component';
import {TransportationeditComponent} from './transportationedit/transportationedit.component';
import {ListvehicleComponent} from './listvehicle/listvehicle.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {SavevehicleComponent} from './savevehicle/savevehicle.component';


const routes: Routes = [
  {
    path: '', component: TransportationComponent, children: [
      {path: 'dashboard', component: DashboardComponent},
      {path: 'editProfile', component: TransportationeditComponent},
      {path: 'saveVehicle', component: SavevehicleComponent},
      {path: 'manageVehicle', component: ListvehicleComponent},
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TransportationRoutingModule {
}
