import {TransportationActions} from './transportation.actions';
import {TransportationModel} from '../models/transportation.model';
import {VehicleModel} from '../models/vehicle.model';
import {AppState} from '../../../../store/app.reducer';

export interface TransportationState extends AppState {
  transportation: State;
}

export interface State {
  transportation: TransportationModel;
  vehicles: VehicleModel[];
  currentVehicle: VehicleModel;
}

const initialState: State = {
  transportation: new TransportationModel(),
  vehicles: [],
  currentVehicle: new VehicleModel()
};

export function TransportationReducer(state = initialState, action: TransportationActions) {
  switch (action.type) {
    case 'SET_VEHICLES': {
      console.log('inside set vehicles reducer');
      console.log(action.payload);
      return {
        ...state,
        vehicles: action.payload
      };
    }

    case 'SET_TRANSPORTATION': {
      console.log('setting transporatation');
      return {
        ...state,
        transportation: action.payload
      };
    }

    case 'SET_CURRENT_VEHICLE': {
      return {
        ...state,
        currentVehicle: action.payload
      };
    }
    default: {
      return {
        ...state
      };
    }

  }
}
