import {Component, OnInit} from '@angular/core';
import {Store} from '@ngrx/store';
import {TransportationState} from './store/transportation.reducer';
import {TransportationModel} from './models/transportation.model';
import {ActivatedRoute, Router} from '@angular/router';
import * as TransportationActions from './store/transportation.actions';
import {first} from 'rxjs/operators';

@Component({
  selector: 'app-transportation',
  templateUrl: './transportation.component.html',
  styleUrls: ['./transportation.component.css']
})
export class TransportationComponent implements OnInit {

  transportation: TransportationModel;

  constructor(private store: Store<TransportationState>, private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.store.dispatch(new TransportationActions.TryFetchTransportation());

    this.store.select('transportation').pipe(first()).subscribe(
      value => {
        // console.log(value);
        if (value.transportation.profilePercentage === 0 || value.transportation.profilePercentage === undefined) {
          this.router.navigate(['editProfile'], {relativeTo: this.route});
        } else {
          this.router.navigate(['dashboard'], {relativeTo: this.route});
        }
      }
    );
  }

}
