import {Component, OnInit, ViewChild} from '@angular/core';
import {VehicleModel} from '../models/vehicle.model';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Store} from '@ngrx/store';
import * as TranState from '../store/transportation.reducer';
import {ActivatedRoute, Router} from '@angular/router';
import * as TransportationActions from '../../transportation/store/transportation.actions';
import * as RealEstateActions from '../../realestate/store/realestate.actions';

@Component({
  selector: 'app-savevehicle',
  templateUrl: './savevehicle.component.html',
  styleUrls: ['./savevehicle.component.css']
})
export class SavevehicleComponent implements OnInit {

  @ViewChild('t', {static: false}) tabSet;
  id: any;
  vehicle: VehicleModel = new VehicleModel();
  editVehicleForm: FormGroup;
  imageForm: FormGroup;

  vehicleFitnessImage: File = null;
  vehicleRcImage: File = null;
  vehicleInsuranceImage: File = null;
  vehiclePucImage: File = null;

  previewUrlRc: any = '../../../assets/icons/uploadPic.png';
  previewUrlInsurance: any = '../../../assets/icons/uploadPic.png';
  previewUrlPuc: any = '../../../assets/icons/uploadPic.png';
  previewUrlFitness: any = '../../../assets/icons/uploadPic.png';

  constructor(private fb: FormBuilder, private store: Store<TranState.TransportationState>, private route: ActivatedRoute, private router: Router) {
  }

  ngOnInit() {
    this.editVehicleForm = this.fb.group({
      registrationNo: [this.vehicle.registrationNo, Validators.required],
      registrationYear: [this.vehicle.registrationYear ? this.vehicle.registrationYear : '2019', Validators.required],
      registrationState: [this.vehicle.registrationState ? this.vehicle.registrationState : 'Delhi', Validators.required],
      vehicleBrand: [this.vehicle.vehicleBrand, Validators.required],
      noOfSeats: [this.vehicle.noOfSeats, Validators.required],
      vehicleColour: [this.vehicle.vehicleColour, Validators.required],
      kmsCovered: [this.vehicle.kmsCovered, Validators.required],
      insurancePolicyNo: [this.vehicle.insurancePolicyNo, Validators.required]
    });

    this.imageForm = this.fb.group({
      vehicleRcImage: [''],
      vehicleInsuranceImage: [''],
      vehiclePucImage: [''],
      vehicleFitnessImage: ['']
    });

    this.store.select('transportation').subscribe(
      (vehicleState: TranState.State) => {
        this.vehicle = vehicleState.currentVehicle;
        this.editVehicleForm.patchValue(vehicleState.currentVehicle);
        this.imageForm.patchValue(vehicleState.currentVehicle);
      }
    );
  }

  imgPreviewVehicleRc(fileInput: any) {
    this.vehicleRcImage = fileInput.target.files[0] as File;
    const mimeType = this.vehicleRcImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.vehicleRcImage);
    reader.onload = () => {
      this.previewUrlRc = reader.result;
    };
  }

  imgPreviewInsurance(fileInput: any) {
    this.vehicleInsuranceImage = fileInput.target.files[0] as File;
    const mimeType = this.vehicleInsuranceImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.vehicleInsuranceImage);
    reader.onload = () => {
      this.previewUrlInsurance = reader.result;
    };
  }

  imgPreviewPuc(fileInput: any) {
    this.vehiclePucImage = fileInput.target.files[0] as File;
    const mimeType = this.vehiclePucImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.vehiclePucImage);
    reader.onload = () => {
      this.previewUrlPuc = reader.result;
    };
  }

  imgPreviewFitness(fileInput: any) {
    this.vehicleFitnessImage = fileInput.target.files[0] as File;
    const mimeType = this.vehicleFitnessImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.vehicleFitnessImage);
    reader.onload = () => {
      this.previewUrlFitness = reader.result;
    };
  }

  onSubmitImages() {
    const data = new FormData();
    data.append('vehicleRcImage', this.vehicleRcImage);
    data.append('vehicleInsuranceImage', this.vehicleInsuranceImage);
    data.append('vehiclePucImage', this.vehiclePucImage);
    data.append('vehicleFitnessImage', this.vehicleFitnessImage);
    data.append('_id', this.vehicle._id);

    this.store.dispatch(new TransportationActions.UploadVehicleImages(data));
    this.router.navigate(['../dashboard'], {relativeTo: this.route});
  }

  onSubmit() {
    this.tabSet.select('images');
    this.dispatch(this.editVehicleForm.value);
  }

  private dispatch(obj) {
    Object.assign(this.vehicle, obj);
    this.store.dispatch(new TransportationActions.SaveVehicles(this.vehicle));
  }

}
