import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {VehicleModel} from '../models/vehicle.model';
import {Store} from '@ngrx/store';
import * as TranState from '../store/transportation.reducer';
import * as TranAction from '../store/transportation.actions';
import * as AuthState from '../../../../auth/store/auth.reducer';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-listvehicle',
  templateUrl: './listvehicle.component.html',
  styleUrls: ['./listvehicle.component.css']
})
export class ListvehicleComponent implements OnInit, OnDestroy {

  filter = new FormControl('');
  vehicles: VehicleModel[];
  addVehicleForm: FormGroup;
  currentVehicle = new VehicleModel();

  constructor(
    private store: Store<TranState.TransportationState>,
    private fb: FormBuilder,
    private authStore: Store<AuthState.AuthState>,
    private route: ActivatedRoute,
    private router: Router
  ) {
  }


  ngOnInit(): void {

    this.store.dispatch(new TranAction.TryFetchVehicles());

    this.store.select('authState').subscribe(
      (auth: AuthState.AuthState) => {
        this.addVehicleForm = this.fb.group({
          transportationMode: ['Road', Validators.required],
          vehicleType: ['Car', Validators.required],
          ownerId: [auth.profile._id, Validators.required]
        });
      }
    );

    this.store.select('transportation').subscribe(
      (vehicleState: TranState.State) => {
        this.vehicles = vehicleState.vehicles;
      }
    );
  }

  onSubmit() {
    Object.assign(this.currentVehicle, this.addVehicleForm.value);
    this.store.dispatch(new TranAction.SaveVehicles(this.currentVehicle));
  }

  editVehicle(index: number) {
    this.store.dispatch(new TranAction.SetCurrentVehicle(this.vehicles[index]));
    this.router.navigate(['../saveVehicle'], {relativeTo: this.route});
  }

  search(): VehicleModel[] {
    const text: string = this.filter.value;
    return this.vehicles.filter(vehicle => {
      const term = text.toLowerCase();
      return vehicle.registrationNo.toLowerCase().includes(term)
        || vehicle.vehicleBrand.toLowerCase().includes(term)
        || vehicle.registrationState.toLowerCase().includes(term)
        || vehicle.vehicleType.toLowerCase().includes(term);
    });
  }

  ngOnDestroy(): void {
  }
}
