import {Component, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Store} from '@ngrx/store';
import {TransportationModel} from '../models/transportation.model';
import {TransportationState} from '../store/transportation.reducer';
import * as TransportationActions from '../store/transportation.actions';
import {ActivatedRoute, Route, Router} from '@angular/router';
import {templateJitUrl} from '@angular/compiler';

@Component({
  selector: 'app-transportationedit',
  templateUrl: './transportationedit.component.html',
  styleUrls: ['./transportationedit.component.css']
})
export class TransportationeditComponent implements OnInit {

  @ViewChild('t', {static: false}) tabSet;

  navigationCheck = false;
  editForm: FormGroup;
  ownerForm: FormGroup;
  imageForm: FormGroup;

  registrationMsmeImage: File = null;
  registrationGstImage: File = null;
  registrationCinImage: File = null;

  previewImage: any = '../../../assets/icons/uploadPic.png';

  previewUrlCinProof: any = '../../../assets/icons/uploadPic.png';
  previewUrlregistrationMsmeNumber: any = '../../../assets/icons/uploadPic.png';
  previewUrlgstNumber: any = '../../../assets/icons/uploadPic.png';

  transportationStore: TransportationModel;

  constructor(private fb: FormBuilder, private store: Store<TransportationState>, private router: Router, private route: ActivatedRoute) {

  }

  ngOnInit() {
    this.imageForm = this.fb.group({
      registrationCinProof: [''],
      gstNumberProof: [''],
      registrationMsmeNumberProof: ['']
    });

    this.ownerForm = this.fb.group({
      ownerDetails: this.fb.group({
        profileRegistrar: ['OWNER', Validators.required],
        name: ['', Validators.required],
        mobile: ['', Validators.required],
        email: ['', Validators.required],
        pan: ['', Validators.required],
        address: this.fb.group({
          address: [''],
          pincode: [''],
        })
      }),
      bankDetails: this.fb.group({
        bankName: [''],
        bankAccountNo: [''],
        bankIfsc: [''],
        bankAddress: ['']
      }),
    });


    this.editForm = this.fb.group({

      name: ['', Validators.required],
      registrationType: ['privateLimited', Validators.required],
      registrationNo: ['', Validators.required],
      officeAddress: ['', Validators.required],
      officePostalCode: ['', Validators.required],
      registrationAuthority: ['', Validators.required],
      registrationMsmeNumber: ['', Validators.required],
      gstNumber: ['', Validators.required],
      panNumber: ['', Validators.required],
      tanNumber: ['', Validators.required],
      unionEnrollmentNumber: ['', Validators.required],
      mobile: ['', Validators.required]
    });

    this.store.select('transportation').subscribe(
      value => {
        console.log(value);
        this.transportationStore = value.transportation;

        this.previewUrlCinProof = value.transportation.registrationCinProof ? value.transportation.registrationCinProof : this.previewImage;
        this.previewUrlgstNumber = value.transportation.gstNumberProof ? value.transportation.gstNumberProof : this.previewImage;
        this.previewUrlregistrationMsmeNumber = value.transportation.registrationMsmeNumberProof ? value.transportation.registrationMsmeNumberProof : this.previewImage;

        this.editForm.patchValue(value.transportation);
        this.imageForm.patchValue(value.transportation);

        if (value.transportation.ownerDetails !== undefined) {
          this.ownerForm.patchValue(value.transportation);
        }
      }
    );
  }


  imgPreviewregistrationMsmeNumber(fileInput: any) {
    this.registrationMsmeImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationMsmeImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationMsmeImage);
    reader.onload = () => {
      this.previewUrlregistrationMsmeNumber = reader.result;
    };
  }

  imgPreviewgstNumber(fileInput: any) {
    this.registrationGstImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationGstImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationGstImage);
    reader.onload = () => {
      this.previewUrlgstNumber = reader.result;
    };
  }

  imgPreviewaddress(fileInput: any) {
    this.registrationCinImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationCinImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationCinImage);
    reader.onload = () => {
      this.previewUrlCinProof = reader.result;
    };
  }

  onSubmit() {
    if (this.navigationCheck) {
      this.navigationCheck = false;
      // this.router.navigate(['../dashboard'], {relativeTo: this.route});
    } else {
      this.tabSet.select('ownerDetails');
    }
    this.dispatch(this.transportationStore, this.editForm.value);
  }

  onOwnerSubmit() {
    this.dispatch(this.transportationStore, this.ownerForm.value);
    if (this.navigationCheck) {
      this.navigationCheck = false;
      this.router.navigate(['../dashboard'], {relativeTo: this.route});
    } else {
      this.tabSet.select('imagesForm');
    }
  }

  onImageSubmit() {
    const data = new FormData();
    data.append('registrationCinProof', this.registrationCinImage);
    data.append('gstNumberProof', this.registrationGstImage);
    data.append('registrationMsmeNumberProof', this.registrationMsmeImage);
    this.store.dispatch(new TransportationActions.UploadTransportationImages(data));
  }

  private dispatch(obj1, obj2) {
    Object.assign(obj1, obj2);
    this.store.dispatch(new TransportationActions.TransportationEdit(this.transportationStore));
  }

  navigationTrigger() {
    alert('inside navigation trigger');
    this.navigationCheck = true;
  }
}
